import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function effectiveTaxRateTtm(tableName: string) {
  const title = "Effective Tax Rate (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["effectiveTaxRateTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Average tax rate paid on earnings.",
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about effective tax rate?`,
              value: info.row.original.financialData.companyRatios?.data?.effectiveTaxRateTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            (info.row.original.financialData.companyRatios?.data?.effectiveTaxRateTtm ?? 0) * 100
          }
        />
      )
    },
  })
}

export { effectiveTaxRateTtm }
